@import url("https://fonts.googleapis.com/css2?family=Ubuntu&display=swap");

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    font-family: "Ubuntu", sans-serif;
    background-color: var(--bgColor);
    color: cornsilk;
    --mainColor: #e96e50;
    --bgColor: rgb(15, 15, 15);
    font-size: large;
}

body {
    text-align: center;
    max-width: 600px;
    margin: 0px auto;
}

.searchBox {
    font-size: x-large;
    text-align: center;
    width: 95%;
    margin: 10px auto;
    outline: 0;
    padding: 5px;
    border: 1px solid var(--mainColor);
}

.box {
    height: calc(100vh - 65px);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    font-size: 25px;
}

.top,
.down {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 160px;
}

.mid > div {
    color: var(--mainColor);
}

.box img {
    width: 170px;
}

.curTemp {
    margin-top: 25px;
    color: var(--mainColor);
    font-size: 65px;
}
